body {
  overflow: hidden;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



.dataPoint {
  padding-left: 4% !important;
}

.dataColumn {

}

.mainPageWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.calculatorWrapper {
  margin: 2%;
  padding: 2%;
  -ms-flex: 1 1;
      flex: 1 1;

}

.news {
  -ms-flex: 1 1;
      flex: 1 1;
  margin: 2%;
}


#snackbarContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  position: fixed;
  bottom: 0;
  z-index: 101;
  width: 30%;
}

.removed {
  margin-top: -9.5em;
}

.paper-snackbar {
  -webkit-transition-property: opacity, bottom, left, right, width, margin, border-radius;
  -o-transition-property: opacity, bottom, left, right, width, margin, border-radius;
  transition-property: opacity, bottom, left, right, width, margin, border-radius;
  -webkit-transition-duration: 0.5s;
       -o-transition-duration: 0.5s;
          transition-duration: 0.5s;
  -webkit-transition-timing-function: ease-in;
       -o-transition-timing-function: ease-in;
          transition-timing-function: ease-in;
  font-family: RobotoDraft;
  font-size: 14px;
  min-height: 14px;
  background-color: #222;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  color: white;
  line-height: 22px;
  padding: 18px 24px;
  bottom: 0;
  opacity: 0;

}

.paper-snackbar p {
  padding: 1em;
}

.paper-snackbar a {
  color: #039be5;
}

@media (min-width: 640px) {
  .paper-snackbar {
    /*
Desktop:
  Single-line snackbar height: 48 dp tall
  Minimum width: 288 dp
  Maximum width: 568 dp
  2 dp rounded corner
  Text: Roboto Regular 14 sp
  Action button: Roboto Medium 14 sp, all-caps text
  Default background fill: #323232 100%
*/
    min-width: 288px;
    max-width: 100%;
    display: -ms-inline-flexbox;
    display: inline-flex;
    margin: 24px;
    bottom: -100px;
  }

  .paper-snackbar .action {
    margin: 0px 0px 0px 24px;
  }

  .removed {
    margin-top: -9.5em;
  }
}


@media (min-width: 640px) and (orientation: landscape) and (max-width:  1024px){
  #snackbarContainer {
    width: 100%;
  }

  .paper-snackbar {
    margin: 0;
    margin-top: 1%;
  }
  .removed {
    margin-top: -9.5em;
  }
}

@media (min-width: 640px) and (orientation: portrait) and (max-width:  1024px) {
  #snackbarContainer {
    width: 100%;
  }

  .paper-snackbar {
    margin: 0;
    margin-top: 1%;
  }
  .removed {
    margin-top: -9.5em;
  }
}

@media (max-width: 640px) {
  .paper-snackbar {
    /*
Mobile:
Single-line snackbar height: 48 dp
Multi-line snackbar height: 80 dp
Text: Roboto Regular 14 sp
Action button: Roboto Medium 14 sp, all-caps text
Default background fill: #323232 100%
*/
    left: 0px;
    right: 0px;
    width: 100% !important;
  }

  #snackbarContainer {
    width: 100%;
  }

  .paper-snackbar .action {
    margin: none;
  }
  .removed {
    margin-top: -10em;
  }
}

.paper-snackbar .action {
  background: inherit;
  display: inline-block;
  border: none;
  font-size: inherit;
  text-transform: uppercase;
  min-width: -webkit-min-content;
  min-width: -moz-min-content;
  min-width: min-content;
}


